.gallery-container {
  width: 1440px;
  flex-wrap: wrap;
  flex-basis: calc(25% - 20px);
  gap: 40px;
  margin: 100px;
  display: flex;
}

a {
  text-decoration: none;
}

.gallery {
  width: 100%;
  gap: 30px;
  display: flex;
}

.gallery-item {
  width: 300px;
}

img {
  width: 490px;
  max-width: 100%;
  height: 300px;
  object-fit: cover;
  transition: transform .3s ease-in-out;
  display: block;
}

.form-div {
  width: 100%;
  background-color: rgba(0, 0, 0, .827);
  margin-bottom: 30px;
  padding: 0 15px;
  position: fixed;
  top: 0;
  box-shadow: 0 5px 10px 2px rgba(0, 0, 0, .5);
}

.search-form {
  max-width: 400px;
  text-align: center;
  margin: 0 auto;
  padding: 15px 0;
  position: relative;
}

.search {
  width: 100%;
  height: 42px;
  color: #4b4b4b;
  background: #e2e0e0;
  border: 2px solid #dfdfdf;
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
}

.submit-btn {
  width: 42px;
  height: 42px;
  cursor: pointer;
  background: #dfdfdf;
  border: none;
  border-radius: 0 5px 5px 0;
  position: absolute;
  top: 15px;
  right: 0;
}

.submit-btn:before {
  content: "üîç";
  color: #f9f0da;
  font-size: 20px;
}

.photo-card img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  border-radius: 5px 5px 0 0;
  box-shadow: 0 3px 3px 1px rgba(34, 60, 80, .2);
}

.info {
  width: 300px;
  height: 60px;
  color: #000;
  background-color: orange;
  border-radius: 0 0 5px 5px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  list-style: none;
  display: flex;
  box-shadow: 0 3px 3px 1px rgba(34, 60, 80, .2);
}

.info-item {
  text-align: center;
  flex-direction: column;
  padding: 0 10px;
  font-family: Roboto, Open Sans, sans-serif;
  font-size: 12px;
  line-height: 1.4;
  text-decoration: none;
  display: flex;
}

.count {
  display: block;
}

.image-link {
  max-width: 400px;
  height: 260px;
  flex-grow: 1;
  display: block;
  overflow: hidden;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: rgba(32, 34, 36, .737);
  border: none;
  border-radius: 7px;
  align-items: center;
  margin: 30px auto;
  padding: 15px 35px;
  font-size: 22px;
  font-weight: bold;
  display: flex;
  box-shadow: 0 1px 10px 2px rgba(0, 0, 0, .2);
}

.load-more:hover {
  background-color: rgba(0, 0, 0, .953);
}

.is-hidden {
  display: none;
}

